export const getClassmates = async (classCode, forceRefresh = false, tab = 'unknown') => {
  if (!db) throw new Error("Firestore  .");
  if (!classCode || classCode === '') {
    console.warn("[firebase.js] getClassmates:    ");
    return [];
  }

  const cacheKey = `classmates_${classCode}`;

  if (!forceRefresh) {
    const cached = getCache(cacheKey, tab);
    if (cached) {
      return cached;
    }
  }

  try {
    // []       .
    const usersRef = collection(db, "users");
    const q = originalFirebaseQuery(usersRef, originalFirebaseWhere("classCode", "==", classCode));
    const querySnapshot = await getDocs(q);

    const classMembers = querySnapshot.docs.map(doc => ({
      id: doc.id,
      uid: doc.id, // AuthContext   uid 
      ...doc.data()
    }));

    logDbOperation('READ', 'users', null, { tab, extra: `getClassmates(${classCode}): ${classMembers.length}` });

    setCache(cacheKey, classMembers);
    return classMembers;

  } catch (error) {
    console.error(`[firebase.js]     (${classCode}):`, error);
    throw error;
  }
};

