const getAllUsersDocuments = async (useCache = true) => {
  if (!db) throw new Error("Firestore  .");
  
  const cacheKey = 'users_all';
  
  if (useCache) {
    const cached = getCache(cacheKey);
    if (cached) {
      console.log(`[firebase.js] getAllUsersDocuments:  ${cached.length} `);
      return cached;
    }
  }
  
  try {
    const usersCollectionRef = collection(db, "users");
    
    //  []   
    let usersSnapshot;
    try {
      usersSnapshot = await getDocsFromCache(usersCollectionRef);
      console.log(`[firebase.js] getAllUsersDocuments:  `);
    } catch (cacheError) {
      usersSnapshot = await getDocs(usersCollectionRef);
      console.log(`[firebase.js] getAllUsersDocuments:  `);
    }
    
    const result = usersSnapshot.docs.map((d) => ({ id: d.id, ...d.data() }));
    
    if (useCache) {
      setCache(cacheKey, result);
    }
    
    console.log(`[firebase.js] getAllUsersDocuments: ${result.length} `);
    return result;
  } catch (error) {
    console.error("[firebase.js] getAllUsersDocuments :", error);
    throw error;
  }
};

