const updateUserCashInFirestore = async (userId, amount, logMessage = '', senderInfo = null, receiverInfo = null, allowNegative = false) => {
  if (!db) throw new Error("[firebase.js] Firestore  .");
  if (!userId) throw new Error("[firebase.js]  ID  .");
  if (typeof amount !== "number")
    throw new Error("[firebase.js]    .");

  //  [] AuthContext         
  // invalidateCache(`user_${userId}`); //   

  const userRef = doc(db, "users", userId);
  try {
    await runTransaction(db, async (transaction) => {
      const userSnap = await transaction.get(userRef);
      if (!userSnap.exists()) {
        throw new Error(
          `[firebase.js]  (ID: ${userId})   .`
        );
      }
      const currentCash = userSnap.data().cash || 0;
      if (!allowNegative && amount < 0 && currentCash + amount < 0) {
        throw new Error(
          `[firebase.js]  . (: ${currentCash},  : ${amount})`
        );
      }
      transaction.update(userRef, {
        cash: increment(amount),
        updatedAt: serverTimestamp(),
      });
    });

    //    
    let activityLogMessage = logMessage;
    let logType = amount > 0 ? ' ' : ' ';

    if (senderInfo && receiverInfo) {
      if (amount > 0) {
        logType = ' ';
        activityLogMessage = `${senderInfo.name} ${amount} .${senderInfo.message ? ` : "${senderInfo.message}"` : ''}`;
      } else {
        logType = '';
        activityLogMessage = `${receiverInfo.name} ${Math.abs(amount)} .${senderInfo.message ? ` : "${senderInfo.message}"` : ''}`;
      }
    } else if (senderInfo && amount > 0) {
      logType = ' ';
      if (senderInfo.isAdmin) {
        activityLogMessage = ` ${senderInfo.name} ${amount} .${senderInfo.reason ? ` : ${senderInfo.reason}` : ''}`;
      } else {
        activityLogMessage = `${senderInfo.name || ''} ${amount} .`;
      }
    } else if (receiverInfo && amount < 0) {
      logType = ' ';
      if (receiverInfo.isAdmin) {
        activityLogMessage = ` ${receiverInfo.name} ${Math.abs(amount)} .${receiverInfo.reason ? ` : ${receiverInfo.reason}` : ''}`;
      } else {
        activityLogMessage = `${receiverInfo.name || ''} ${Math.abs(amount)} .`;
      }
    } else if (logMessage) {
      activityLogMessage = logMessage;
    } else {
      const defaultMessage = `${Math.abs(amount)} ${amount > 0 ? '' : ''} .`;
      activityLogMessage = defaultMessage;
    }

    //  [] Activity Log   (   )
    addActivityLog(userId, logType, activityLogMessage).catch(err =>
      console.error('[firebase.js] Activity Log   ():', err)
    );

    console.log(
      `[firebase.js]  ${userId}  ${amount > 0 ? "+" : ""}${amount}   .`
    );
    return true;
  } catch (error) {
    console.error(
      `[firebase.js]  ${userId}    :`,
      error
    );
    return false;
  }
};

