export const transferCash = async (senderId, receiverId, amount, message = '', allowNegative = false) => {
  if (!senderId || !receiverId || amount <= 0) {
    throw new Error('   .');
  }

  const senderRef = doc(db, "users", senderId);
  const receiverRef = doc(db, "users", receiverId);

  try {
    await runTransaction(db, async (transaction) => {
      // 1.    
      const [senderSnap, receiverSnap] = await Promise.all([
        transaction.get(senderRef),
        transaction.get(receiverRef)
      ]);

      if (!senderSnap.exists()) {
        throw new Error('   .');
      }
      if (!receiverSnap.exists()) {
        throw new Error('   .');
      }

      const senderData = senderSnap.data();
      const receiverData = receiverSnap.data();

      // 2.  
      if (!allowNegative && (senderData.cash || 0) < amount) {
        throw new Error(' .');
      }

      // 3.    
      transaction.update(senderRef, { cash: increment(-amount), updatedAt: serverTimestamp() });
      transaction.update(receiverRef, { cash: increment(amount), updatedAt: serverTimestamp() });
    });

    //   ,       (  )
    const senderDoc = await getUserDocument(senderId, true); //   
    const receiverDoc = await getUserDocument(receiverId, true);

    const senderName = senderDoc?.name || '   ';
    const receiverName = receiverDoc?.name || '   ';
    
    const senderLogMessage = `${receiverName} ${amount} .${message ? ` : ''${message}''` : ''}`;
    const receiverLogMessage = `${senderName} ${amount} .${message ? ` : ''${message}''` : ''}`;

    await Promise.all([
      addActivityLog(senderId, '', senderLogMessage),
      addTransaction(senderId, -amount, `: ${receiverName}`),
      addActivityLog(receiverId, ' ', receiverLogMessage),
      addTransaction(receiverId, amount, ` : ${senderName}`)
    ]);
    
    //  
    invalidateCache(`user_${senderId}`);
    invalidateCache(`user_${receiverId}`);

    console.log(`[firebase.js]   : ${senderId} -> ${receiverId}, : ${amount}`);
    return { success: true, amount };

  } catch (error) {
    console.error('   :', error);
    throw error; // UI      
  }
};

