const addItemToInventory = async (
  userId,
  storeItemId,
  quantity,
  itemDetails = {}
) => {
  if (!db) throw new Error("Firestore is not initialized.");
  if (
    !userId ||
    !storeItemId ||
    typeof quantity !== "number" ||
    quantity <= 0
  ) {
    throw new Error(" ID,  ID    .");
  }
  
  const inventoryColRef = collection(db, "users", userId, "inventory");
  const q = originalFirebaseQuery(
    inventoryColRef,
    originalFirebaseWhere("itemId", "==", storeItemId)
  );
  const querySnapshot = await getDocs(q);

  const itemName = itemDetails.name || "Unknown Item";

  if (!querySnapshot.empty) {
    const inventoryDoc = querySnapshot.docs[0];
    const inventoryItemRef = doc(
      db,
      "users",
      userId,
      "inventory",
      inventoryDoc.id
    );
    await updateDoc(inventoryItemRef, {
      quantity: increment(quantity),
      updatedAt: serverTimestamp(),
    });
    console.log(
      `[firebase.js]   (${inventoryDoc.id})  ${quantity} `
    );
  } else {
    const newItemRef = doc(inventoryColRef);
    await setDoc(newItemRef, {
      itemId: storeItemId,
      quantity: quantity,
      name: itemName,
      icon: itemDetails.icon || "",
      type: itemDetails.type || "item",
      purchasedAt: serverTimestamp(),
      updatedAt: serverTimestamp(),
    });
    console.log(
      `[firebase.js]   (${newItemRef.id}) : ${quantity}`
    );
  }

  await addActivityLog(userId, " ", `${itemName} ${quantity} .`);
  return true;
};

