export const getUserInventory = async (userId, useCache = true, tab = 'unknown') => {
  if (!db) throw new Error("Firestore  .");
  if (!userId) throw new Error(" ID .");

  const cacheKey = `inventory_${userId}`;

  if (useCache) {
    const cached = getCache(cacheKey, tab);
    if (cached) {
      return cached;
    }
  }

  try {
    const inventoryRef = collection(db, "users", userId, "inventory");
    const inventorySnapshot = await getDocs(inventoryRef);
    const inventory = inventorySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));

    logDbOperation('READ', `users/${userId}/inventory`, null, { tab, extra: `${inventory.length} ` });

    if (useCache) {
      setCache(cacheKey, inventory);
    }

    return inventory;
  } catch (error) {
    console.error(`[firebase.js]    (: ${userId}):`, error);
    throw error;
  }
};

