export const getActivityLogs = async (classCode, limit = 50, useCache = false, tab = 'unknown') => {
  if (!db) throw new Error("Firestore  .");
  if (!classCode) throw new Error("  .");

  const cacheKey = `activity_logs_${classCode}_${limit}`;

  //  []       
  if (useCache) {
    const cached = getCache(cacheKey, tab);
    if (cached) {
      return cached;
    }
  }

  try {
    const logsRef = collection(db, "activity_logs");
    const q = originalFirebaseQuery(
      logsRef,
      originalFirebaseWhere("classCode", "==", classCode)
    );

    const logsSnapshot = await getDocs(q);

    let logs = logsSnapshot.docs.map((doc) => {
      const data = doc.data();
      return {
        id: doc.id,
        ...data,
        timestamp: data.timestamp?.toDate ? data.timestamp.toDate() : data.timestamp
      };
    });

    logDbOperation('READ', 'activity_logs', null, { tab, extra: `${classCode}: ${logsSnapshot.docs.length}` });

    //    (  )
    logs.sort((a, b) => {
      if (!a.timestamp && !b.timestamp) return 0;
      if (!a.timestamp) return 1;
      if (!b.timestamp) return -1;

      const timeA = a.timestamp instanceof Date ? a.timestamp.getTime() : new Date(a.timestamp).getTime();
      const timeB = b.timestamp instanceof Date ? b.timestamp.getTime() : new Date(b.timestamp).getTime();

      return timeB - timeA; //  
    });

    //  
    if (limit > 0) {
      logs = logs.slice(0, limit);
    }

    //  [ v3.0]   - 5 TTL (globalCacheService )
    if (useCache) {
      globalCacheService.set(`fb_${cacheKey}`, logs, 5 * 60 * 1000); // 5 TTL
    }

    return logs;
  } catch (error) {
    console.error(`[firebase.js]     (: ${classCode}):`, error);
    return [];
  }
};

